* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Rubik Distressed, cursive;
}

body {
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

button[data-start], button[data-stop] {
  cursor: pointer;
  width: 500px;
  height: 148px;
  border-radius: 70px;
  margin: 50px;
  font-size: 72px;
  position: relative;
  box-shadow: 0 10px 10px rgba(140, 140, 140, .3);
}

